                        <button type="button" class="btn btn-secondary" data-dismiss="modal">  <i class="mdi mdi-close-circle font-size-18"></i> Voltar</button>                        
                        <button type="submit" class="btn btn-primary" :disabled=" $v.form.$invalid || sucesso || form.id > 0" ><i class="mdi mdi-content-save font-size-18"></i> Salvar </button>
                        <button v-if="form.id" type="button" class="btn btn-success waves-effect waves-light w-sm" title="Imprimir" @click="imprimir()">
                            <i class="bx bx-printer d-block font-size-16"></i> Imprimir
                        </button>
                         <button v-if="form.id" data-dismiss="modal" @click="novaVenda()" class="btn btn-success waves-effect waves-light w-sm" title="Nova Venda" >
                                <i class="bx d-block bxs-plus-square font-size-16"></i> Nova Venda
                            </button>
                        
