<script>

import { required, requiredIf, decimal ,minValue, maxLength, } from "vuelidate/lib/validators";
import { ProdutoService, EmpresaService, ParceiroService, EstoqueService, NotaTransferenciaService ,PlanoContasService } from '../../../services'
// import { validarCnpj } from "../../../herpers/helpers";
import moment from 'moment';
import {mask} from 'vue-the-mask'

const produtoService = new ProdutoService();
const empresaService = new EmpresaService();
const parceiroService = new ParceiroService();
const estoqueService = new EstoqueService();
const notaTransferenciaService = new NotaTransferenciaService();
const planoContasService = new PlanoContasService();


export default {
    directives: {mask},
    data() {
        return {
            
            form: {
                id: null,
                empresa_id: '',
                estoque_id: '',
                
                numero: '',
                data_emissao: '',

                documento_frete:'',
                custo_frete: '',
                custo_outros: '',
                observacoes_custo: '',

                valor_subtotal: 0,
                valor_desconto: 0,
                valor_acrescimo: 0,
                valor_total: 0,

                nota_itens: [],

                caixa_id: ''
            },

            empresas: [],
            empresa: {},
            estoques:[],
            estoques_destino: [],
            estoque:{},
            
            parceiros: [],
            parceiro:{},
            buscandoParceiros: false,
          
            sucesso: null,
            erros:null,
            errosTitulo:'',
              
            timer: null,

            produtos: [],
            buscandoProdutos: false,


            erroItemModal: null,
            sucessoItemModal:null,
            itemModal: {},

            preco:{
                decimal: ',',
                thousands: '.',
                prefix: this.$store.state.moeda + ' ',
                suffix: '',
                precision: 2,
                masked: false
            },

            percentual:{
                decimal: ',',
                thousands: '.',
                prefix: '',
                suffix: ' %',
                precision: 2,
                masked: false
            },

            desconto_percentual_mascara:{
                decimal: ',',
                thousands: '.',
                prefix: '',
                suffix: '',
                precision: 1,
                masked: false
            },
            desconto_valor_mascara:{
                decimal: ',',
                thousands: '.',
                prefix: '',
                suffix: '',
                precision: 2,
                masked: false
            },
            

            contas: [],
            buscandoContas: false,

            timer_desconto: null,
            editando_desconto_valor: false,
            editando_desconto_percentual: false,
        }        
        
    },
    validations() {
        return {

            itemModal: {
                produto_codigo: {required},
                valor_unitario: {required, minValue: minValue(0.0001)},
                qtde: {required, minValue: minValue(0.0001)},
                valor_desconto: {required, minValue: minValue(0)},
                valor_acrescimo: {required, minValue: minValue(0)}
            },

            form: {
                empresa_id: {required},
                estoque_id: {required},
                
                documento_frete:{maxLength:maxLength(60)},
                custo_frete: {},
                custo_outros: {},
                observacoes_custo: {maxLength:maxLength(200)},

                valor_subtotal: {required, minValue: minValue(0.0001)},
                valor_desconto: {required, minValue: minValue(0)},
                valor_acrescimo: {required, minValue: minValue(0)},
                valor_total: {required, minValue: minValue(0.001)},

                nota_itens: {
                    required,
                    $each: {
                        produto_codigo: {required},
                        valor_unitario: {required, minValue: minValue(0.0001)},
                        qtde: {required, minValue: minValue(0.0001)},
                        valor_desconto: {required, minValue: minValue(0)},
                        valor_acrescimo: {required, minValue: minValue(0)},
                        estoque_id: {required}
                    }
                }
                
                
            },
        }
        

    },
    mounted() {

        setTimeout(() => {
            this.buscarEmpresas();    
            this.buscarEstoques();
        }, 10);
            
        
        
        setTimeout(() => {
            this.buscarContas();
        }, 2000);

        if (this.$route.params.id) {
            setTimeout(() => {
                this.buscarObjeto(this.$route.params.id);    
            }, 400);
        }else{
            this.adicionarItem();
        }
    },
    watch: {        
        
        'form.valor_acrescimo': function (val) {
            this.calcularValorTotal()
        },
        


  
    },
    methods: {

        selecionarEstoqueOrigem: function(){
            setTimeout(() => {
                this.estoques_destino = this.estoques.filter(x => x.id != this.estoque.id);    
                this.form.estoque_id = this.estoque.id;
            }, 50);
            
        },

        imprimir: function(){
            window.open(`${this.$store.state.url}/print/nota-transferencia/${this.form.id}`, '_blank')
        },

        novaVenda: function(){
            this.$router.go({name: 'notaTransferenciaLancar'})
            this.form = {
                id: null,
                // empresa_id: '',
                // estoque_id: '',
                parceiro_id: '',
                numero: '',
                data_emissao: '',

                documento_frete:'',
                custo_frete: '',
                custo_outros: '',
                observacoes_custo: '',

                valor_subtotal: 0,
                valor_desconto: 0,
                valor_acrescimo: 0,
                valor_total: 0,

                nota_itens: [ ],

                
            }

            this.adicionarItem();
        },

        key: function(e, tab=false){
            if(this.form.id > 0)
                return false;
            switch(e.key){
                case 'Insert':
                    this.adicionarItem();
                    break;
                case 'Tab':
                     if(tab) this.adicionarItem();
                    break;
            }
        },

        validarFormulario: function(){
            this.$v.form.$touch();
            this.erros = null;
            this.sucesso = null;    
            this.errosTitulo ='';

            if (this.$v.form.$invalid){
                this.errosTitulo = 'Preencha o formulário corretamente, alguns campos estão com erros!'
                this.erros = this.$v.form;
                return false;
            }
            return true;
        },

        submit: function(){

            if(!this.validarFormulario())
                return false;
            let obj = {...this.form};
            
            let request = null;
            if(obj.id)    
                request = notaTransferenciaService.atualizar(obj);
            else
                request = notaTransferenciaService.criar(obj);

            request.then(
                response => {
                    let redireciona = !this.form.id;
                    this.form = response.data;
                    this.sucesso = "Registro salvo com sucesso!";
                    if(redireciona) {
                        let index = String(this.$route.path).lastIndexOf("/");
                        let newPath = String(this.$route.path).slice(0, index);
                        newPath += "/" + this.form.id;
                        this.$router.push(newPath);
                        this.buscarObjeto(this.form.id);
                    }
                }   
            ).catch( erro => this.erros = erro )

        },

        calcularValorTotal: function(){
            this.form.valor_subtotal = this.soma_itens;
            this.form.valor_total = this.form.valor_subtotal - this.form.valor_desconto + this.form.valor_acrescimo;
            
        },

        calculaValorsubTotalItem: function(item){
            item.valor_subtotal =  ((item.qtde * item.valor_unitario) + item.valor_acrescimo)
            this.calcularValorTotal();
        },

        adicionarItem(){
            let i = this.estoques.map(x => x.id).indexOf(this.estoque.id);
            this.form.nota_itens.push({
                qtde: 1,
                valor_acrescimo: 0,
                valor_desconto: 0,
            })
        },

        selecionarProduto(item){
            
            item.produto_codigo=item.produto.codigo; 
            item.produto_descricao=item.produto.descricao; 
            item.produto_id=item.produto.id;
            item.valor_unitario = Number(item.produto.preco_custo);
            this.calculaValorsubTotalItem(item);

        },

        salvarItem: function(){
            
            this.erroItemModal = null;
            this.sucessoItemModal = null;
            this.$v.itemModal.$touch();
            if(this.$v.itemModal.$invalid){
                this.erroItemModal = this.$v.itemModal;
                return 0;
            }
            let item = {...this.itemModal};
            if(item.index >= 0)
                this.form.nota_itens.splice(item.index, 1, item);                
            else
                this.form.nota_itens.push(item);
                
            this.sucessoItemModal = `Produto ${this.itemModal.produto_descricao} adicionado com sucesso!`
        },

        removerItem: function(index){     
            //abrir modal
            this.form.nota_itens.splice(index, 1);
            Vue.$toast.success(`Item #${(index+1)} Removido`)
        },


        qtde: function(decimais){
                return {
                    decimal: ',',
                    thousands: '.',
                    prefix: '',
                    suffix: '',
                    precision: decimais,
                    masked: false
                }
            },

        buscarObjeto: function(id){
            notaTransferenciaService.buscar(id).then(  response => {
                this.form = response.data;
                this.form.custo_frete = Number(response.data.custo_frete);
                this.form.custo_outros = Number(response.data.custo_outros);
                this.form.valor_subtotal = Number(response.data.valor_subtotal);
                this.form.valor_desconto = Number(response.data.valor_desconto);
                this.form.valor_acrescimo = Number(response.data.valor_acrescimo);
                this.form.valor_total = Number(response.data.valor_total);

                this.form.data_emissao = moment(this.form.data_emissao).toDate();
                this.empresas = [response.data.empresa];
                this.empresa = this.empresas[0];
                if(response.data.parceiro){
                    this.parceiros = [response.data.parceiro];
                    this.parceiro = this.parceiros[0];
                }

                this.form.nota_itens = this.form.nota_itens.map(x => {
                    x.valor_unitario = Number(x.valor_unitario);
                    x.qtde = Number(x.qtde);
                    x.valor_acrescimo = Number(x.valor_acrescimo);
                    x.valor_desconto = Number(x.valor_desconto);
                    x.valor_subtotal = Number(x.valor_subtotal);

                    return x;
                })      
                    
            }).catch( erro => this.erros = erro)
        },

        buscarEmpresas: function(){
            let key = this.user.id +"_empresas";
            let empresas = localStorage.getItem(key);
            if(empresas != null){
                empresas = JSON.parse(atob(empresas));
                let currentDate = new Date().getTime();
                if(empresas.expires > currentDate){
                     this.empresas = empresas.empresas;
                    if(this.empresas.length == 1){
                        this.empresa = this.empresas[0];
                        this.form.empresa_id = this.empresa.id
                    }
                    return true;
                }else{
                    localStorage.removeItem(key);
                }
            }

            empresaService.listar({ativa:true})
                .then(  response => {
                    this.empresas = response.data.data;
                    if(this.empresas.length == 1){
                        this.empresa = this.empresas[0];
                        this.form.empresa_id = this.empresa.id
                    }

                    let expires = new Date();
                    expires = expires.setHours( expires.getHours() + 6 );
                    let str = JSON.stringify({
                        expires: expires,
                        empresas: this.empresas
                    });
                    localStorage.setItem(key, btoa(str));


                }).catch( erro => console.log('Erros: ', erro.response))
        },

        buscarEstoques: function(){
            
            let key = this.user.id +"_estoques";
            let estoques = localStorage.getItem(key);
            
            if(estoques != null){
                estoques = JSON.parse(atob(estoques));
                let currentDate = new Date().getTime();
                if(estoques.expires > currentDate){
                     this.estoques = estoques.estoques;
                    
                    this.estoque = this.estoques[0];
                    this.form.nota_itens[0].estoque_id = this.estoque.id;
                    this.form.nota_itens[0].estoque = this.estoques[0];

                    this.selecionarEstoqueOrigem();
                    return true;
                }else{
                    localStorage.removeItem(key);
                }
            }

            estoqueService.listar({ativo:true})
                .then(  response => {
                    this.estoques = response.data.data;
                    
                    this.estoque = this.estoques[0];
                    this.form.nota_itens[0].estoque_id = this.estoque.id;
                    this.form.nota_itens[0].estoque = this.estoques[0];

                    let expires = new Date();
                    expires = expires.setHours( expires.getHours() + 6 );
                    let str = JSON.stringify({
                        expires: expires,
                        estoques: this.estoques
                    });
                    localStorage.setItem(key, btoa(str));
                    this.selecionarEstoqueOrigem();

                }).catch( erro => console.log('Erros: ', erro.response))
        },

        buscarParceirosDebounce: function(text, loading){
            clearTimeout(this.timer);
            this.timer = setTimeout(() => {
                this.buscarParceiros(text, loading)
            }, 700);
        },

        buscarParceiros: function(text, loading){
            this.buscandoParceiros = true;
            parceiroService.listar({texto: text, per_page: 50, id: this.form.id, tipo: 'CLIENTE'})
                .then( response =>  this.parceiros = response.data.data )
                .catch( error => console.log(error))
                .finally( () => this.buscandoParceiros = false)
        },


        buscarProdutosDebounce: function(text, loading){
            clearTimeout(this.timer);
            this.timer = setTimeout(() => {
                this.buscarProdutos(text, loading)
            }, 700);
        },

        buscarProdutos: function(text, loading){
            this.buscandoProdutos = true;
            let objBusca = {
                texto: text, 
                per_page: 50, 
                order: 'descricao', 
                venda: 1,
                id: this.estoque.id
            };

            produtoService.listar(objBusca)
                .then( response =>  this.produtos = response.data.data.map( x => {
                    x.preco_compra = Number(x.preco_compra);
                    x.preco_custo = Number(x.preco_custo);
                    x.label = `${x.codigo} | ${x.descricao}`
                    return x;
                }) )
                .catch( error => console.log(error))
                .finally( () => this.buscandoProdutos = false)
        },

        buscarContasDebounce: function(){
            clearTimeout(this.debounce);
            this.debounce = setTimeout(() => {
                this.buscarContas();
            }, 800);
        },
        
        buscarContas:function(){

            

            
            this.buscandoContas = true;
            planoContasService.listar({tipo: "TRANSFERENCIA", per_page: 50}).then(
                response => {
                    this.contas = response.data.data.map(x => {
                        return {id: x.id, label: `${x.codigo} | ${x.nome}`}
                    });
                    
            }).finally( () => this.buscandoContas = false)
        },


        desabilitaDataFutura: function(data) {
            let hoje = new Date();
            return data > hoje
        },

      

    },
    computed:{
        user () {
            return this.$store.state.user
        },
        subtotal_geral_itens(){
            return this.form.nota_itens.reduce((ac,at) =>  {
                return (ac + ((at.valor_unitario > 0 ? Number(at.valor_unitario) : 0) * (at.qtde > 0 ? Number(at.qtde) : 0)))
                }, 0);
        },

        despesas_geral(){
            return Number(this.form.valor_acrescimo) + Number(this.form.custo_frete) + Number(this.form.custo_outros) - Number(this.form.valor_desconto);
        },
        total_geral(){
            return Number(this.form.valor_subtotal) + Number(this.form.valor_acrescimo) + Number(this.form.custo_frete) + Number(this.form.custo_outros) - Number(this.form.valor_desconto);
        },
        qtde_itens(){
            return this.form.nota_itens.length;
        },
        qtde_itens_total(){
            return this.form.nota_itens.reduce((ac,at) =>  ac + (at.qtde > 0 ? parseFloat(at.qtde) : 0), 0);
        },
        soma_itens(){
            return this.form.nota_itens.reduce((ac,at) =>  {
                return (ac + ((at.valor_unitario > 0 ? Number(at.valor_unitario) : 0) * (at.qtde > 0 ? Number(at.qtde) : 0)) + (at.valor_acrescimo > 0 ? Number(at.valor_acrescimo) : 0))
                }, 0);
        },
        saldo(){
            return this.total_geral - this.soma_itens;
        },
        pode_trocar_estoque_origem(){
            return !(this.form.id > 0) && this.form.nota_itens.filter(x => x.produto).length == 0
        }

        
    }

   
};
</script>
